.footer {
    max-width: 880px;
    padding: 66px 0 60px 0;
    margin: 0 auto;
}
@media screen and (min-width: 768px) and (max-width: 1023px) {
    .footer {
            padding: 66px 19px 60px 19px;
        }
}
@media screen and (min-width: 320px) and (max-width: 767px) {
    .footer {
            padding: 48px 19px 36px 19px;
        }
}